'use strict';


import React from 'react';
import PropTypes from 'prop-types'

import Subheader from 'material-ui/Subheader'
import TextField from 'material-ui/TextField'
import Paper from 'material-ui/Paper'
import RaisedButton from 'material-ui/RaisedButton'
import Divider from 'material-ui/Divider'
import SelectField from 'material-ui/SelectField'
import MenuItem from 'material-ui/MenuItem';
import Snackbar from 'material-ui/Snackbar';

import './EditView.scss';

let EditView = (props) => {
  let openSnackbar = !!(props.isSaved && props.id);
  let snackbarMessage =  openSnackbar ? `${props.name} has been created` : '';
  let className = 'organization-edit-view ' + ((props.isSaving) ?  'state-saving' : '');
  let idDisplay = !!props.id && <span className="id-display">id: {props.id}</span>;
  let errors = (props.errors && props.errors.length>0) && <div className="errors">
      An error occured when trying save this Organization.
      <ul>
      {props.errors.map((err, i)=>{
         return <li>{err.message}</li>
      })}
      </ul>
    </div>;

  let invalidSettingsErrorText = props.settingsInvalid ? 'Invalid JSON' : false;
  let settings = typeof props.settings === 'string' ? props.settings : JSON.stringify(props.settings);

  // TODO - SEttings...
  return <div className={className}>
      {errors}
      {idDisplay}
      <TextField floatingLabelText="Name" disabled={props.isSaving} onChange={(evt, val)=>props.changeHandler('name', val)} value={props.name}/>
      <br/>
      <TextField errorText={invalidSettingsErrorText}
                 floatingLabelText="Settings"
                 disabled={props.isSaving}
                 multiLine={true}
                 rows={3}
                 onChange={(evt, val)=>props.changeHandler('settings', val)}
                 value={settings} />
      <br/>

      <Snackbar bodyStyle={{ height: 100, lineHeight: '32px', flexGrow: 0 }}
                open={openSnackbar}
                message={snackbarMessage}
                autoHideDuration={2500} />

    </div>
};

export default EditView;


