'use strict';


import React, { Component } from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import {actions} from '../modules/organizations';
import OrganizationView from '../components/OrganizationView'
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';

const mapStateToProps = (state) => {
  return {...state.organizations};
};

const mapDispatchToProps = {
  saveItem : actions.saveItem
};

class OrganizationContainer extends Component {
  static propTypes = {
    errors: PropTypes.any,
    isSaving: PropTypes.bool.isRequired,

    id: PropTypes.string, // null if creating new, not null for edit
    name: PropTypes.string.isRequired,
    settings: PropTypes.object.isRequired,
    onExit: PropTypes.func.isRequired,
    saveItem: PropTypes.func.isRequired,
    isEditing: PropTypes.bool
  };

  static defaultProps = {
    name: "",
    settings: {},
    settingsInvalid: false
  };

  getStateFromProps(props){
    let settingsInvalid = null;
    return {
      id: props.id,
      name: props.name,
      settings: {
        ...props.settings
      },
      settingsInvalid
    };
  }

  constructor(props){
    super(props);
    this.state = this.getStateFromProps(props);
  }

  onSave = () => {
    this.props.saveItem({...this.state});
  }

  onExit = () => {
    this.props.onExit();
  }

  onChange = (propName, newVal) => {
    let state = {};
    if (propName === 'settings') {
      try {
        newVal = JSON.parse(newVal);
        state.settingsInvalid = null;
      } catch (err) {
        state.settingsInvalid = true;
      }
    }

    state[propName] = newVal;
    this.setState(state);
  };


  render () {
    let orgViewProps = {
      errors: this.props.errors,
      isSaving: this.props.isSaving,
      isEditing: this.props.isEditing,
      settingsInvalid: this.state.settingsInvalid,
      settings: this.state.settings,
      name: this.state.name,
      id: this.state.id,
      onExit: this.onExit,
      onChange: this.onChange,
      onSave: this.onSave
    };

    return <OrganizationView {...orgViewProps} />
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(OrganizationContainer)



