'use strict';


import React from 'react'
import PropTypes from 'prop-types'
import Dialog from 'material-ui/Dialog';
import FlatButton from 'material-ui/FlatButton';
import EditView from './EditView'

let OrganizationView = (props)=>{
  let editViewProps = {
    name : props.name,
    settings: props.settings,
    settingsInvalid: props.settingsInvalid,
    id: props.id,
    changeHandler: props.onChange
  };

  let disabledSave = props.settingsInvalid || props.isSaving;
  let label = props.isSaving ? 'Saving...' : 'Save';

  const actions = [
    <FlatButton
      label={label}
      disabled={disabledSave}
      primary={true}
      onTouchTap={props.onSave}
      keyboardFocused={true}
    />,
    <FlatButton
      label="Close"
      secondary={true}
      onTouchTap={props.onExit}
    />
  ];

  return <Dialog open={props.isEditing}
                 modal={false}
                 actions={actions}
                 onRequestClose={props.onExit}
                 autoScrollBodyContent={true}
          >
    <EditView {...editViewProps} />
  </Dialog>;
};

OrganizationView.propTypes = {
  errors: PropTypes.any,
  isSaving: PropTypes.bool.isRequired,
  isEditing: PropTypes.bool,

  id: PropTypes.string, // null if creating new, not null for edit
  name: PropTypes.string.isRequired,
  settings: PropTypes.object.isRequired,
  settingsInvalid: PropTypes.bool,

  onExit: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired
};

export default OrganizationView;



