'use strict';
import React, { Component } from 'react';
import PropTypes from 'prop-types'

import OrganizationContainer from './OrganizationContainer';
import ListView from '../components/ListView';
import ListControls from '../components/ListControls';

import { connect } from 'react-redux'
import { actions } from '../modules/organizations'
import findById from '../../../helpers/findById';

const mapDispatchToProps = {
  ...actions
};

const mapStateToProps = (state) => {
  return {
    ...state.organizations
  }
};

class Container extends Component {
  static propTypes = {
    isRequesting: PropTypes.bool.isRequired,
    hasReceived: PropTypes.bool.isRequired,
    errors: PropTypes.any.isRequired,
    items: PropTypes.array.isRequired,
    isSaving: PropTypes.bool.isRequired
  }

  constructor(props){
    super(props);
    this.state = {
      isEditing: false
    }
  }

  onEnter = (id)=>{
    let organization = findById(id, this.props.items);
    this.setState({
      organization,
      isEditing: true
    });
  }

  onExit = ()=>{
    this.setState({
      organization: null,
      isEditing: false
    });
  }

  componentDidMount() {
    if (!this.props.isRequesting && !this.props.hasReceived) {
      this.props.getItems();
    }
  }

  componentWillReceiveProps(nextProps){
    if (this.props.isSaving && !nextProps.isSaving){
      this.setState({isEditing:false});
    }
  }

  render () {
    let editCreate = this.state.isEditing && <OrganizationContainer isEditing={this.state.isEditing} onExit={this.onExit} {...this.state.organization} />;
    return <div style={{
      'width': '380px',
      'margin': '1rem auto 0'
    }}>
        <ListControls {...this.props} onCreate={this.onEnter} />
        <ListView {...this.props} onItemEdit={this.onEnter} />
        {editCreate}
      </div>
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Container)


