'use strict';

import {Enum} from 'enumify';
const generateList = (noun, list, plural)=> {

  plural = plural || noun + 'S';
  list = list || [
    `REQUESTING_${plural}`,
    `RECEIVED_${plural}`,
    `RECEIVED_${plural}_ERROR`,
    `ONCHANGE_SELECTED_${plural}`,
    `DELETING_${plural}`,
    `DELETED_${plural}`,
    `DELETED_${plural}_ERROR`,
    `CREATING_${noun}`,
    `CREATED_${noun}`,
    `CREATED_${noun}_ERROR`,
    `ADDED_${noun}`,
    `UPDATING_${noun}`,
    `UPDATED_${noun}`,
    `UPDATED_${noun}_ERROR`,
    `CHANGE_DRAFT_${noun}`
  ];

  class ACTION_TYPES extends Enum {}
  ACTION_TYPES.initEnum(list);

  return ACTION_TYPES;
}

export default generateList;



