'use strict';

import React, {Component} from 'react';
import PropTypes from 'prop-types'
import './ListView.scss'
import ItemView from './ItemView'
import {List} from 'material-ui/List';


const TYPE_NAME = 'organization';

class ListView extends Component {

  onItemEdit(id){
    this.props.onItemEdit(id);
  }

  render() {
    let {props, state, onItemEdit} = this;
    let rows = props.items.map((item, i) => {
      let {id} = item;
      return <ItemView key={`${TYPE_NAME}-item-${id}`} {...item} onEdit={()=>this.onItemEdit(id)} />
    });

    return <div className={`${TYPE_NAME}-list-view`}>
      <List>
        {rows}
      </List>
    </div>
  }
}

ListView.defaultProps = {
  items: [],
  errors: false,
  onItemEdit : (id)=>{
    console.log('editing org: ', id);
  }
}

ListView.propTypes = {
  items: PropTypes.array.isRequired,
  hasReceived: PropTypes.bool,
  isRequesting: PropTypes.bool,
  errors: PropTypes.any.isRequired,
  onItemEdit: PropTypes.func.isRequired
}

export default ListView;




