'use strict';

import client from '../../../helpers/ApiClient';
import _pick from 'lodash/pick';
import upsertById from '../../../helpers/upsertById';

// ------------------------------------
// Constants
// ------------------------------------
import ACTION_TYPES from '../../../actionTypes/organizations';

const DEFAULT_STATE = {
  items : [],
  isRequesting: false,
  hasReceived: false,

  errors: false,
  isSaving: false
};

/*  This is a thunk, meaning it is a function that immediately
 returns a function for lazy evaluation. It is incredibly useful for
 creating async actions, especially when combined with redux-thunk! */

const getItems = () => (dispatch, getState) => {
  dispatch({
    type: ACTION_TYPES.REQUESTING_ORGANIZATIONS
  });

  return client.get('/organizations?filter[order]=name ASC')
    .then(items => {
      dispatch({
        type: ACTION_TYPES.RECEIVED_ORGANIZATIONS,
        items
      });
    }).catch(errors => {
      dispatch({
        type: ACTION_TYPES.RECEIVED_ORGANIZATIONS_ERROR,
        errors
      });
    });
};

const saveItem = (item) => (dispatch, getState) => {
  let data = {...item};
  data.settings = item.settings || {};

  if (typeof data.id === 'undefined') {
    delete data.id;
  }

  dispatch({
    type: ACTION_TYPES.UPDATING_ORGANIZATION,
    item
  });

  let url = `/organizations${(!!item.id ? `/${item.id}` : '')}`;
  return client.put(url, {data})
    .then(item=>{
      dispatch({
        type: ACTION_TYPES.UPDATED_ORGANIZATION,
        item
      })
    }).catch(errors => {
      dispatch({
        type: ACTION_TYPES.UPDATED_ORGANIZATION_ERROR,
        errors
      })
    })
}

const _getUpdatedItem = function(dispatch, actionType, {data:{id}}) {
  return client.get(`/organizations/${id}`).then(item=>{
    return dispatch({
      type: actionType,
      item
    });
  });
}

export const actions = {
  getItems,
  saveItem
}

export default function reducer(state = DEFAULT_STATE, action) {
  let newState = Object.assign({}, state);
  switch(action.type) {
    case ACTION_TYPES.REQUESTING_ORGANIZATIONS:
      return Object.assign({}, state, {
        isRequesting:true,
        errors: false
      });

    case ACTION_TYPES.RECEIVED_ORGANIZATIONS:
      return Object.assign({}, state, {
        isRequesting:false,
        hasReceived: true,
        items: action.items
      });

    case ACTION_TYPES.RECEIVED_ORGANIZATIONS_ERROR:
      return Object.assign({}, state, {
        isRequesting:false,
        errors: action.errors
      });

    case ACTION_TYPES.UPDATING_ORGANIZATION:
      return (()=>{
        return Object.assign({}, state, {
          errors: false,
          isSaving: true
        })
      })();

    case ACTION_TYPES.UPDATED_ORGANIZATION:
      return (()=> {
        // Avoids duplicates
        let items = upsertById(action.item, state.items);
        return Object.assign({}, state, {
          items,
          isSaving: false
        });
      })();

    default:
      return state;
  }
};




